*{
  margin:0;
  box-sizing:border-box;
}

html{
  width:100vw;
  height:100vh;
}

body {
  font: 14px "Century Gothic", Futura, sans-serif;
  background: #000;
  color:#fff;
  display:flex;
  justify-content:center;
  align-items:center;
  height:100vh;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 2px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 50px;
  height: 50px;
  cursor:pointer;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  justify-content:center;
  align-items:center;
  flex-wrap:wrap;
  width:100%;
  min-width:320px;
}

.game-info {
  margin-left: 20px;
}

@media screen and (max-width:640px){
  .game-info{
    margin-top:20px;
    margin-left: 0;
  }
}